<template>
  <div id="inviteListAll" :class="{hasNavBar: $utils.showNavBar()}">
    <HeaderNav v-if="$utils.showNavBar()"></HeaderNav>
    <div class="scrollBoxFix">
      <div class="scrollBox" ref="inviteList">
        <div>
          <div class="" v-if="list.length > 0" v-for="(item,index) in list" @click="setItem(index)" :key="index">
            <!-- 正常状态 -->
            <div class="inviteItem flex-row jusB" v-if="item.status === 1">
              <div class="leftContent flex-row">
                <div class="leftIcon iconSize">
                  <img :src="item.invitee_avatar" alt="头像">
                </div>
                <div class="nickname">{{$utils.splitStr(item.invitee_nickname, 6)}}</div>
              </div>
              <div class="rightContent flex-column level-flex-end">
                <div class="top flex-row hoz-flex-end time">{{item.create_time ? $utils.timestampToStr(item.create_time * 1000, 'h:m:s') :'无'}}</div>
                <div class="top flex-row hoz-flex-end date">{{item.create_time ? $utils.timestampToStr(item.create_time * 1000, 'Y-M-D') :'无'}}</div>
              </div>
            </div>
            <!-- 失效状态 -->
            <div class="inviteItem flex-row failure jusB" @click="setItem(index)" v-else>
              <div class="leftContent flex-row">
                <div class="leftIcon iconSize">
                  <img :src="item.invitee_avatar" alt="头像">
                </div>
                <div class="nickname">{{$utils.splitStr(item.invitee_nickname, 6)}}</div>
                <div class="statusButton flex-row">
                  <span>失效</span>
                </div>
              </div>
              <div class="rightContent flex-column jusC">
                <div class="top flex-row hoz-flex-end date">{{item.create_time ? $utils.timestampToStr(item.create_time * 1000, 'Y-M-D') :'无'}}</div>
              </div>
            </div>
          </div>
          <div class="noList" v-else>
            <div class="flex-row">
              <div class="iconSize">
                <img src="../../../assets/img/task/no_invite_list.png" alt="无邀请列表">
              </div>
            </div>
            <div class="flex-row">
              <div class="f24 color999">你还没有邀请过好友</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
import HeaderNav from '@/components/task/header-nav'
import {mapState} from 'vuex'
export default {
  components: {
    HeaderNav
  },
  data () {
    return {
      list: [],
      page: 1,
      loadSuccess: 0, // 0 未加载完成 1为加载完成
      eventDirection: ''
    }
  },
  computed: mapState({
    commonState: state => state.common
  }),
  methods: {
    getInviteList (cb) {
      this.$api.get('INVITE_LIST', {page: this.page}, res => {
        // res.data.data = this.countList()
        typeof cb === 'function' && cb(res.data.data)
        this.scroll.finishPullUp()
        if (this.list.length >= res.data.total) {
        // if (this.list.length >= 1000) {
          this.scroll.closePullUp()
        }
      })
    },
    setItem (index) {
      let item = this.list[index]
      if (item.status === 0) {
        this.$toast({
          message: '该账户数据被清除，已停用',
          duration: 1500
        })
      }
    },
    // 上拉加载
    upList () {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      this.timer = setTimeout(() => {
        this.getInviteList(res => {
          this.list = [...this.list, ...res]
          this.loadList()
        })
      }, 200)
    },
    // 下拉刷新
    downList () {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      this.timer = setTimeout(() => {
        this.getInviteList(res => {

        })
      }, 200)
    },
    // 初始化数据
    loadList () {
      this.$nextTick(() => {
        if (!this.scroll) {
          let that = this
          console.log(this.$refs.inviteList)
          this.scroll = new BScroll(this.$refs.inviteList, {
            click: true,
            probeType: 3,
            bounce: false,
            pullDownRefresh: {
              threshold: 50,
              stop: 0
            },
            pullUpLoad: true,
            mouseWheel: {
              speed: 10,
              invert: false,
              easeTime: 300
            },
            bindToWrapper: true,
            eventPassthrough: that.eventDirection,
            scrollbar: {
              fade: true,
              interactive: false
            }
          })
          this.scroll.on('pullingDown', () => {
            this.downList()
          })
          this.scroll.on('pullingUp', () => {
            this.upList()
          })
        } else {
          this.$nextTick(() => {
            this.scroll.refresh()
          })
        }
      })
    },
    // 计算假数据
    countList () {
      let arr = []
      for (let i = 0; i < 20; i++) {
        let tempData = JSON.parse(JSON.stringify(this.list[0]))
        tempData.nickname = tempData.nickname + i
        arr.push(tempData)
      }
      return arr
    }
  },
  watch: {
    'commonState.login_box' (flag) {
      if (!flag && this.commonState.login_bol) {
        this.getInviteList(res => {
          // res[0].status = 0
          this.page = 1
          this.complete = false
          this.inviteList = []
          this.list = res
          this.loadList()
        })
      }
    }
  },
  created () {
    this.getInviteList(res => {
      // res[0].status = 0
      this.list = res
      this.loadList()
    })
  }
}
</script>
<style lang="scss" scoped>
#inviteListAll{
  width: 100%;
  height: 100%;
  overflow: hidden;
  .scrollBoxFix{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    .scrollBox{
      height: 100%;
    }
  }
  &.hasNavBar{
    .scrollBoxFix{
      top: 88px;
    }
  }
  .noList{
    .flex-row{
      &:nth-child(1) {
        padding: 156px 0 30px;
      }
      .iconSize{
        width: 169px;
        height: 107px;
      }
    }
    
  }
  .inviteItem {
    padding: 20px 30px;
    font-size: 30px;
    color: #232323;
    border-bottom: 1px solid #d8d8d8;
    .leftIcon{
      width: 70px;
      height: 70px;
      margin-right: 20px;
      overflow: hidden;
      border-radius: 50%;
    }
    .rightContent{
      font-size: 24px;
      color: #999;
      height: 60px;
      display: flex;
      justify-content: space-between;
    }
    .statusButton {
      margin-left: 14px;
      background: #999;
      font-size: 26px;
      color: #fff;
      border-radius: 44px;
      box-sizing: border-box;
      width: 104px;
      height: 44px;
      span {
        padding-top: 4px;
      }
    }
    &.failure {
      background: #eee;
      .rightContent {
        justify-content: center;
      }
    }
  }
}
</style>



